import React , {Component} from 'react';
import { Media } from 'reactstrap';
import {Card,CardImg,CardImgOverlay, CardText,CardBody,CardTitle} from 'reactstrap';
import DishDetail from './DishdetailComponent';
import './MenuComponents.css';
class Menu extends Component{
    constructor(props){
        super(props);
    this.state ={
        selectedDish : null
    };
    }

    onDishSelect(dish){
        this.setState({selectedDish:dish})
    }

    render(){
        const menu = this.props.dishes.map((dish)=>{
            return(
                <div key={dish.id} className="col-12 col-md-5 m-1"> 
                    <Card onClick={()=>this.onDishSelect(dish)} style={{ height: '25rem' }}>
                        <CardImg height= "100%" width="100%" object src={dish.image} alt={dish.name}/>
                        <CardImgOverlay body className="ml-5">
                        <CardTitle heading>{dish.name}</CardTitle>
                        </CardImgOverlay>
                    </Card>
                </div>
            );
        });
        return(
            <div  className='container'>
            <div className='row'>
                    {menu}
            </div>
            <div className='row'>
            <DishDetail selectedDish={this.state.selectedDish}/>
             {window.scrollTo(document.scrollingElement.scrollHeight, document.scrollingElement.scrollHeight)}
            </div>
            </div>
        );
    }
}

export default Menu;